@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  /* font-family: "Roboto", sans-serif; */
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 500;
  color: black;
  font-size: 14px;
  line-height: 22px;
  background: #f1f2f5;
  font-feature-settings: normal !important;
  font-variant: normal;
  /* overflow-x: hidden; */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
  color: inherit;
  font-weight: 600;
}

ul {
  list-style-type: none;
}
p {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  color: inherit;
}
a:hover {
  color: inherit;
}

button {
  outline: 0;
  border: none;
}

img {
  object-fit: cover;
  object-position: 50%;
}
input:focus {
  outline: 0;
}
.request-btn,
.book-btn {
  padding: 10px 18px;
  border-radius: 5px;
  color: white;
  font-weight: 600;
  cursor: pointer;
}
.mobile-top-btn {
  display: none;
}
.search input {
  color: white;
  border: none;
  width: 100%;
  height: 100%;
  padding-left: 16px;
  background-color: #396bb8;
}
.search input::-webkit-input-placeholder {
  color: rgb(155, 152, 152);
  opacity: 1; /* Firefox */
}
.search input:-ms-input-placeholder {
  color: rgb(155, 152, 152);
  opacity: 1; /* Firefox */
}
.search input::placeholder {
  color: rgb(155, 152, 152);
  opacity: 1; /* Firefox */
}
.search svg {
  position: absolute;
  right: 16px;
}
.request-btn {
  margin-left: auto;
  margin-right: 16px;
  border: 2px solid white;
  background-color: transparent;
}
.book-btn {
  background-color: #56c080;
  border: 2px solid #56c080;
}

@media (max-width: 493px) {
  .mobile-top-btn {
    display: block;
    display: flex;
    justify-content: center;
  }
  .request-btn {
    border: 1px solid #d0d5d9;
    color: #81868c;
    margin-left: 0;
  }
}

.app-header {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 16px 30px;
  color: white;
  background: #0747a6;
  display: flex;
  align-items: center;
  z-index: 2;
}
.app-header .search {
  position: relative;
  margin-left: 100px;
  height: 30px;
  width: 350px;
  display: flex;
  align-items: center;
}

@media (max-width: 954px) {
  .app-header .search {
    display: none;
  }
}

@media (max-width: 493px) {
  .app-header button {
    display: none;
  }
}

.toggle {
  cursor: pointer;
  display: inline-block;
}
.toggle-switch {
  display: inline-block;
  background: rgba(143, 155, 179, 0.25);
  border: 1px solid #c5cee0;

  border-radius: 16px;
  width: 58px;
  height: 32px;
  position: relative;
  vertical-align: middle;
  transition: background 0.25s;
}
.toggle-switch:before,
.toggle-switch:after {
  content: "";
}
.toggle-switch:before {
  display: block;

  background-color: white;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 3px;
  left: 4px;
  transition: left 0.25s;
}
/* .toggle:hover .toggle-switch:before {
  background: linear-gradient(to bottom, #fff 0%, #fff 100%);
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.5);
} */
.toggle-checkbox:checked + .toggle-switch {
  background-color: #0746a6;
}
.toggle-checkbox:checked + .toggle-switch:before {
  left: 30px;
}
.toggle-checkbox {
  position: absolute;
  visibility: hidden;
}

.service-toogle {
  display: flex;
  grid-column-gap: 24px;
  -webkit-column-gap: 24px;
          column-gap: 24px;
}
.service-toogle .details span {
  display: block;
}
.service-toogle .details .title {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 4px;
  color: #303030;
}
.service-toogle .details .description {
  font-weight: 400;
  font-size: 14px;
  letter-spacing: 0.1px;
  color: #81868c;
}

.service-type {
  text-align: left;
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 20px;
  border-radius: 4px;
  width: 25%;
  height: 85px;
  font-weight: 400;
  background-color: transparent;
  font-size: 16px;
  line-height: 29px;
  border: 1px solid #d0d5d9;
  color: #81868c;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
  cursor: pointer;
}
.service-type:last-child {
  margin-right: 0;
}
.service-type.selected {
  border: none;
  background: #0746a6;
  color: #fff;
}
.service-type.selected svg path {
  fill: #fff;
}

.service-type svg {
  outline: none;
  box-shadow: none;
  position: absolute;
  bottom: -12px;
  right: -18px;
  height: 60px;
  width: 60px;
}

.new-booking-wrapper {
  max-width: 1240px;
  width: 75%;
  margin: 100px auto 0;
}

.new-booking-wrapper input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-calendar-picker-indicator {
  display: none;
  -webkit-appearance: none;
}
.new-booking-wrapper .header-wrapper {
  margin-bottom: 12px;
}
.new-booking-wrapper .heading {
  font-weight: 600;
  font-size: 26px;
  line-height: 38px;
  color: #303030;
  margin-bottom: 8px;
}
.new-booking-wrapper .sub-text {
  font-weight: 400;
  font-size: 15px;
  letter-spacing: 0.1px;
  color: #81868c;
}
.new-booking-wrapper .card {
  background: #fff;
  padding: 30px 45px;
  border-radius: 4px;
  border: 0;
  box-shadow: 1px 1px 7px rgb(0, 0, 0 / 1%);
  margin-bottom: 16px;
}
.new-booking-wrapper .section-heading {
  font-weight: 600;
  font-size: 20px;
  line-height: 38px;
  margin-bottom: 16px;
  color: #303030;
}

/* service selection */
.new-booking-wrapper .service-type-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px;
  grid-row-gap: 15px;
  row-gap: 15px;
}

/* cargo location */
.new-booking-wrapper .cargo-location {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px;
  grid-row-gap: 15px;
  row-gap: 15px;
  margin-bottom: 32px;
}
.new-booking-wrapper .cargo-direction {
  display: flex;
}
.new-booking-wrapper .cargo-direction .import,
.new-booking-wrapper .cargo-direction .export {
  cursor: pointer;
  background: #fff;
  color: #707070;
  border: 1px solid #dadce0;
  width: 83px;
  padding: 12px;
  text-align: center;
  font-weight: 500;
}
.new-booking-wrapper .cargo-direction .import.selected,
.new-booking-wrapper .cargo-direction .export.selected {
  background: #0747a6;
  color: #fff;
  border: 1px solid #0747a6;
}

.new-booking-wrapper .locations-input {
  display: flex;
  justify-content: space-between;
  width: 76%;
}
.new-booking-wrapper .input-append {
  position: relative;
  height: 48px;
  width: 48%;
  border-radius: 4px;
  padding-left: 43px;
  background: hsla(0, 0%, 77.3%, 0.2);
  border: none;
  opacity: 1;
  color: #303030;
  display: flex;
  align-items: center;
}

.new-booking-wrapper .input-append input {
  height: 100%;
  width: 100%;
  padding-left: 5px;
  background-color: transparent;
  border: none;
}
.new-booking-wrapper .input-append svg {
  position: absolute;
  left: 17px;
}
.new-booking-wrapper .cargo-status {
  display: flex;
  justify-content: space-between;
}
.new-booking-wrapper .cargo-status .input-append {
  width: 30%;
}
.new-booking-wrapper .cargo-status .select-type {
  display: flex;
  align-items: center;
  padding: 0 20px;
  height: 48px;
  width: 30%;
  border-radius: 4px;
  background: hsla(0, 0%, 77.3%, 0.2);
  border: none;
  opacity: 1;
  color: #303030;
}
.new-booking-wrapper .cargo-status .select-type select {
  background-color: transparent;
  border: none;
  width: 100%;
}
.new-booking-wrapper .select-type select:focus {
  border: none;
}

.new-booking-wrapper .cargo-value {
  height: 48px;
  width: 30%;
  border-radius: 4px;
  padding: 0 10px;
  background: hsla(0, 0%, 77.3%, 0.2);
  border: none;
  opacity: 1;
  color: #303030;
  display: flex;
  align-items: center;
}
.new-booking-wrapper .cargo-value input {
  background-color: transparent;
  border: 0;
  width: 100%;
  height: 100%;
}

/* cargo details */

.new-booking-wrapper .section-heading-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.new-booking-wrapper .section-heading-wrapper .dangerous-cargo {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}
.new-booking-wrapper .section-heading-wrapper .dangerous-cargo span {
  margin-left: 10px;
}

.new-booking-wrapper .tab-header {
  position: relative;
  display: inline-flex;
  align-items: center;
  background: #f1f2f5;
  color: #acb1bb;
  border-radius: 9px;
  padding: 3px;
  margin-bottom: 64px;
}
.new-booking-wrapper .tab-header .tab-header-btn {
  border-radius: 6.5px;
  align-self: center;
  text-align: center;
  padding: 10px 30px;
  margin: 0;
  font-weight: 600;
  cursor: pointer;
  display: inline;
}
.new-booking-wrapper .tab-header .tab-header-btn.selected {
  background: #fff;
  color: #0847a6;
  border: 1px solid #e0e0e0;
}

.new-booking-wrapper .tab-content {
  display: flex;
  grid-column-gap: 28px;
  -webkit-column-gap: 28px;
          column-gap: 28px;
}
.new-booking-wrapper .tab-content .input {
  max-width: 25%;
  display: flex;
  align-items: center;
  position: relative;
  border: 1px solid #dadce0;
  box-sizing: border-box;
  width: 100%;
}

.new-booking-wrapper .tab-content .label {
  position: absolute;
  top: -12px;
  left: 10px;
  font-size: 15px;
  text-transform: capitalize;
  background-color: #ffffff;
  padding: 0 5px;
  color: #303030;
}

.new-booking-wrapper .tab-content input {
  border: none;
  width: 100%;
  background-color: transparent;
  font-size: 13px;
  height: 48px;
}
.new-booking-wrapper .tab-content .append {
  position: absolute;
  right: 10px;
  font-weight: 700;
  line-height: 20px;
  text-transform: lowercase;
  color: #303030;
}

/* Additional services */

.new-booking-wrapper .additional-services-wrapper .additional-services {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  grid-row-gap: 32px;
  row-gap: 32px;
}
.new-booking-wrapper .additional-services-wrapper .service-toogle {
  min-width: 50%;
}

@media (max-width: 1176px) {
  .new-booking-wrapper {
    width: 95%;
  }
}
@media (max-width: 932px) {
  .new-booking-wrapper .service-type-wrapper {
    flex-wrap: wrap;
  }
  .new-booking-wrapper .service-type-wrapper .service-type {
    width: 48%;
  }
}
@media (max-width: 630px) {
  .new-booking-wrapper .card {
    padding: 15px 15px !important;
  }
  .new-booking-wrapper .cargo-location {
    flex-wrap: wrap;
  }
  .new-booking-wrapper .locations-input {
    width: 100%;
  }
  .new-booking-wrapper .section-heading-wrapper {
    flex-wrap: wrap;
  }
  .new-booking-wrapper .tab-header {
    margin-bottom: 32px;
  }
  .new-booking-wrapper .tab-content .input {
    max-width: 48%;
  }
}
@media (max-width: 493px) {
  .new-booking-wrapper .locations-input {
    flex-wrap: wrap;
    grid-row-gap: 15px;
    row-gap: 15px;
  }
  .new-booking-wrapper .service-type-wrapper .service-type {
    width: 100%;
  }
  .new-booking-wrapper .cargo-location {
    margin-bottom: 15px;
  }
  .new-booking-wrapper .input-append {
    width: 100%;
  }
  .new-booking-wrapper .cargo-status {
    flex-wrap: wrap;
    grid-row-gap: 15px;
    row-gap: 15px;
  }
  .new-booking-wrapper .cargo-status .input-append {
    width: 100%;
  }
  .new-booking-wrapper .cargo-status .select-type {
    width: 100%;
  }
  .new-booking-wrapper .cargo-value {
    width: 100%;
  }
}

